.addmeeting {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 150;
  background-color: rgba(37, 54, 85, 0.4);
}
.addmeeting-body {
  width: 100%;
  height: 100%;
  display: flex;
  padding: 16px;
  overflow: scroll;
}

.addmeeting-body::-webkit-scrollbar {
  display: none;
}

.addmeeting-modal {
  width: 100%;
  max-width: 485px;
  margin: auto;
  box-shadow: 0 12px 24px 0 rgba(38, 55, 84, 0.1);
  background-color: #ffffff;
  padding: 24px;
  padding-top: 16px;
  border-radius: 15px;
  position: relative;
}

.addmeeting-modal__close {
  width: 15px;
  height: 15px;
  position: absolute;
  top: 16px;
  right: 16px;
  cursor: pointer;
}

.addmeeting-modal__close svg {
  width: 100%;
  height: 100%;
  fill: #a8aaac;
}

.addmeeting-header {
  width: 100%;
  margin-bottom: 16px;
}

.addmeeting-header h2 {
  font-size: 16px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.56;
  letter-spacing: normal;
  text-align: left;
  color: #253655;
}

.addmeeting-name {
  width: 100%;
  margin-bottom: 24px;
}

.addmeeting-name input {
  width: 100%;
  border-radius: 10px;
  border: solid 1px #f2f5f7;
  padding: 12px 16px;
  font-size: 12px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  text-align: left;
  color: #253655;
}

.addmeeting-name input::placeholder {
  opacity: 0.32;
}

.addmeeting-date {
  width: 100%;
  margin-bottom: 24px;
}

.addmeeting-title {
  font-size: 14px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  text-align: left;
  color: #253655;
  margin-bottom: 16px;
}

.addmeeting-date__container {
  width: 100%;
  display: flex;
  align-items: center;
}

.addmeeting-date__picker {
  width: calc(100% * 2.5 / 3);
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.addmeeting-date__input {
  width: 49%;
  position: relative;
}

.addmeeting-date__input svg {
  width: 8px;
  height: 4px;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
  fill: #a8aaac;
}

.addmeeting-date__input input {
  width: 100%;
  border-radius: 10px;
  border: solid 1px #f2f5f7;
  padding: 10px 16px;
  font-size: 12px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  text-align: left;
  color: #253655;
}

.addmeeting-date__to {
  width: calc(100% * 0.5 / 3);
  text-align: center;
  font-size: 12px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  color: #253655;
}

.radiobtn-label {
  display: flex;
  align-items: center;
  cursor: pointer;
  user-select: none;
}

.radiobtn-circul {
  width: 16px;
  height: 16px;
  border: solid 1px #a8aaac;
  background-color: #ffffff;
  border-radius: 50%;
  position: relative;
}

.radiobtn-circul::before {
  content: "";
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #00b9ff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  /* z-index: 1; */
  display: none;
}

.radiobtn-title {
  margin-left: 8px;
  font-size: 13px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.54;
  letter-spacing: normal;
  text-align: left;
  color: #253655;
}

.radiobtn-label input:checked ~ .radiobtn-circul {
  border-color: #00b9ff;
}
.radiobtn-label input:checked ~ .radiobtn-circul::before {
  display: block;
}

.addmeeting-video {
  margin-bottom: 24px;
}

.addmeeting-video__container {
  width: 100%;
  display: flex;
  align-items: center;
}

.addmeeting-video__type {
  width: 50%;
  display: flex;
  align-items: center;
}

.addmeeting-video__radiobtn {
  display: flex;
  margin-left: 24px;
}

.addmeeting-video__radiobtn > div:last-child {
  margin-left: 20px;
}

.addmeeting-calendar {
  width: 100%;
  margin-bottom: 24px;
}

.addmeeting-calendar__container {
  display: flex;
  align-items: center;
}

.addmeeting-calendar__container > div:last-child {
  margin-left: 32px;
}

.addmeeting-people {
  width: 100%;
  margin-bottom: 8px;
}
.addmeeting-people__header {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.addmeeting-people__invite {
  background-color: transparent;
  border: none;
  padding: 0;
  font-size: 13px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.54;
  letter-spacing: normal;
  text-align: right;
  color: #253655;
  margin-bottom: 16px;
}

.addmeeting-people__invite.active {
  color: #00b9ff;
}

.addmeeting-people__list {
  /* width: 100%; */
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-left: -16px;
  margin-right: -16px;
  padding-left: 8px;
  padding-right: 8px;
}

.addmeeting-people__item {
  position: relative;
  padding-left: 8px;
  padding-right: 8px;
  margin-bottom: 8px;
}

.addmeeting-people__item img {
  width: 40px;
  height: 40px;
  border-radius: 8px;
  object-fit: cover;
}

.addmeeting-people__remove {
  width: 14px;
  height: 14px;
  display: block;
  background-color: #ffffff;
  fill: #ea4c89;
  border-radius: 50%;
  position: absolute;
  top: -7px;
  right: 0px;
  cursor: pointer;
}

.addmeeting-person {
  width: 100%;
}

.addmeeting-person__add {
  background-color: transparent;
  border: none;
  padding: 0;
  font-size: 14px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  color: #253655;
  cursor: pointer;
  margin-bottom: 16px;
}

.addmeeting-person__add.active {
  color: #00b9ff;
}

.addmeeting-person__search {
  width: 100%;
  position: relative;
}

.addmeeting-person__search input {
  width: 100%;
  border-radius: 10px;
  border: solid 1px #f2f5f7;
  padding: 12px 16px;
  padding-right: 32px;
  font-size: 12px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  text-align: left;
  color: #253655;
}

.addmeeting-person__search input::placeholder {
  opacity: 0.32;
}

.addmeeting-person__search button {
  position: absolute;
  top: 24px;
  right: 16px;
  transform: translateY(-50%);
  padding: 0;
  background-color: transparent;
  border: none;
  fill: #a8aaac;
}

.addmeeting-submit {
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 32px 16px;
  padding-bottom: 16px;
}

.addmeeting-submit button {
  border-radius: 8px;
  background-color: #00b9ff;
  border: none;
  min-width: 125px;
  padding: 10px 16px;
  font-size: 14px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.21;
  letter-spacing: normal;
  color: #ffffff;
}

.addmeeting-person__search__list__item{
    width: 100%;
    height: auto;
    padding: 8px 15px;
    box-shadow: 0 0 1px 0 #aaa;
    cursor: pointer;
}

.addmeeting-person__search__list__item_image{
  display: inline-block;
    float: left;
    width: 40px;
    height: 40px;
    border-radius: 100%;
    overflow: hidden;
}

.addmeeting-person__search__list__item_image > img{
      max-width: 100%;
    height: auto;
}

.addmeeting-person__search__list__item > .name{
      display: inline-block;
    float: left;
    margin-left: 12px;
    margin-top: 12px;
}

.addmeeting-person__email{
  position: relative;
    padding: 5px 15px;
}

.addmeeting-person__email .addmeeting-people__remove{
      position: relative;
    display: inline-block;
    top: 2px;
    right: 0;
    margin-left: 10px;
}

