.chat {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.chat-body {
  width: 100%;
  height: calc(100vh - 64px);
  padding: 24px 16px;
}

.chat-container {
  width: 100%;
  height: 100%;
  display: flex;
}

.chat-left {
  width: 225px;
  height: 100%;
  flex-shrink: 0;
}

.chat-right {
  width: min-content;
  flex-grow: 1;
  height: 100%;
  padding-left: 24px;
}

.chat-right__body {
  height: 100%;
  width: 100%;
  border-radius: 20px;
  background-color: #f8f9fd;
  padding: 16px;
}

.chat-left__header {
  width: 100%;
  padding-bottom: 22px;
}

.chat-left__header h2 {
  font-size: 21px;
  font-weight: 600;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.48;
  letter-spacing: normal;
  text-align: left;
  color: #253655;
}

.chat-left__buttons {
  width: 100%;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #f2f5f7;
  padding-bottom: 14px;
}

.chat-left__buttons button {
  width: 50%;
  font-size: 14px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  text-align: center;
  color: #253655;
  background-color: transparent;
  border: none;
  padding: 12px;
  border-bottom: 2px solid transparent;
}

.chat-left__buttons button.active {
  color: #00b9ff;
  border-bottom: 2px solid #00b9ff;
}

.chat-left__search {
  width: 100%;
  padding-bottom: 4px;
  position: relative;
}

.chat-left__search input {
  width: 100%;
  border-radius: 10px;
  border: solid 1px #f2f5f7;
  padding: 8px 16px;
  padding-right: 32px;
  font-size: 10px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.6;
  letter-spacing: normal;
  text-align: left;
  color: #253655;
}

.chat-left__search input::placeholder {
  opacity: 0.32;
}

.chat-left__search button {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  background-color: transparent;
  border: none;
  padding: 0;
  padding-right: 10px;
  padding-top: 3px;
}

.chat-left__search button svg {
  fill: #bfbfbf;
}

.chat-tab {
  width: 100%;
}

.chat-tab__body {
  width: 100%;
  height: 100%;
}

.chat-tab__item {
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
  display: none;
  position: relative;
}

.chat-channels__add {
  width: 32px;
  height: 32px;
  background-color: #00b9ff;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  fill: #ffffff;
  position: absolute;
  bottom: 32px;
  right: 20px;
  cursor: pointer;
}

.chat-tab__item::-webkit-scrollbar {
  display: none;
}

.chat-tab__item.active {
  display: block;
}

:root {
  --item-background: #ffffff;
  --item-icon: #000000;
}

.chat-item {
  width: 100%;
  padding: 12px 16px;
  cursor: pointer;
}

.chat-item.blue {
  --item-background: rgba(15, 86, 251, 0.2);
  --item-icon: #0f57fb;
}

.chat-item.green {
  --item-background: rgba(30, 192, 165, 0.2);
  --item-icon: #1ec0a6;
}
.chat-item.orange {
  --item-background: rgba(253, 85, 43, 0.2);
  --item-icon: #fd552b;
}
.chat-item.pink {
  --item-background: rgba(234, 76, 137, 0.2);
  --item-icon: #ea4c89;
}

.chat-item__body {
  width: 100%;
  display: flex;
}

/* .chat-item:hover,
.chat-item:active, */
.chat-item.active {
  border-radius: 15px;
  background-color: #f8f9fd;
}

.chat-item__group {
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  background-color: var(--item-background);
}

.chat-item__group svg {
  width: 14px;
  height: 14px;
  fill: var(--item-icon);
}

.chat-item__image {
  width: 32px;
  height: 32px;
  border-radius: 50%;
}

.chat-item__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
}

.chat-item__right {
  width: min-content;
  flex-grow: 1;
  display: flex;
  align-items: center;
}

.chat-item__name {
  padding: 0 12px;
}

.chat-item__name span {
  font-size: 12px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  text-align: left;
  color: #253655;
}

.chat-item__icon {
  width: 10px;
  margin-left: auto;
  /* display: flex;
  align-items: center;
  justify-content: center; */
}

.chat-item__icon .icon-circul {
  width: 6px;
  height: 6px;
  fill: #ea4c89;
}

.chat-item__icon .icon-volume-x {
  fill: #a8aaac;
}

.message {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.message-header {
  width: 100%;
  display: flex;
  align-items: center;
}

/* .message-header__left {
  width: min-content;
} */

/* .message-header__right {
  width: min-content;
  flex-grow: 1;
} */

.message-chat__item {
  display: none;
}

.message-chat__item.active {
  display: block;
}

.message-chat__item.chat-item {
  white-space: nowrap;
}

.message-chat__item .chat-item__image {
  width: 40px;
  height: 40px;
}

.message-chat__item .chat-item__name {
  display: flex;
  flex-direction: column;
}

.message-chat__item .chat-item__name span {
  font-size: 14px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  text-align: left;
  color: #253655;
}

.message-chat__item .chat-item__name small {
  opacity: 0.7;
  font-size: 10px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.6;
  letter-spacing: normal;
  text-align: left;
  color: #253655;
}

.message-chat__item .chat-item__group {
  width: 40px;
  height: 40px;
}

.message-header__rigth {
  width: min-content;
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.message-button {
  margin: 0 12px;
  display: flex;
  position: relative;
}

.message-button:last-child {
  margin-right: 0;
}

.message-button button {
  background-color: transparent;
  padding: 0;
  border: none;
}

.message-button.phone button svg {
  fill: #409a5d;
}

.message-button.video button svg {
  fill: #ff562d;
}

.message-button.dots button svg {
  fill: #253655;
}

.message-button.search button svg {
  fill: #253655;
}

.message-button.search input {
  margin-right: 12px;
  width: 195px;
  border: none;
  border-bottom: 1px solid rgba(37, 54, 85, 0.1);
  background-color: transparent;
  font-size: 14px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  text-align: left;
  color: #253655;
}

.message-bottom {
  margin-top: auto;
  display: flex;
  align-items: center;
}

.message-bottom__left {
  width: min-content;
  flex-grow: 1;
  padding-right: 10px;
}

.message-bottom__right button {
  border-radius: 10px;
  background-color: #00b9ff;
  border: none;
  padding: 14px 20px;
  font-size: 12px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  text-align: center;
  color: #ffffff;
}

.message-bottom__left-container {
  width: 100%;
  display: flex;
  align-items: center;
  border-radius: 10px;
  background-color: #ffffff;
  padding: 10px 0;
}

.message-bottom__paperclip {
  padding: 0 16px;
  cursor: pointer;
}
.message-bottom__paperclip span {
  display: block;
}

.message-bottom__paperclip span svg {
  fill: #a8aaac;
}

.message-bottom__emoji {
  padding: 0;
  background-color: transparent;
  border: none;
  fill: #a8aaac;
  padding: 0 16px;
}
.message-bottom__input {
  flex: 1;
  height: 24px;
  border: none;
  border-left: 1px solid #f2f5f7;
  padding: 0 11px;
  font-size: 10px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.6;
  letter-spacing: normal;
  text-align: left;
  color: #252525;
}

.message-bottom__input::-webkit-scrollbar {
  opacity: 0.32;
}

.message-center {
  width: 100%;
  flex: 1;
  padding: 16px 0;
  overflow-y: scroll;
  overflow-x: hidden;
}

.message-center::-webkit-scrollbar {
  display: none;
}

.message-own {
  width: 100%;
  display: flex;
}

.message-own__image {
  width: 40px;
  height: 40px;
  position: relative;
}

.message-own__image img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  object-fit: cover;
}

.message-own__image-active {
  position: absolute;
  right: 1px;
  bottom: 8px;
  width: 8px;
  height: 8px;
}
.message-own__image-active svg {
  width: 100%;
  height: 100%;
  border: 1px solid #ffffff;
  border-radius: 50%;
}

.message-own__image-active svg.online {
  fill: #39c666;
}

.message-own__image-active svg.offline {
  fill: #ea4c89;
}

.message-own__content {
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: 4px 10px;
}

.message-name {
  margin-bottom: 8px;
}

.message-name span {
  font-size: 12px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  text-align: center;
  color: #00b9ff;
}

.message-sentence {
  width: 100%;
  max-width: 210px;
  margin-bottom: 8px;
  display: flex;
  flex-direction: column;
}

.message-sentence__own {
  width: max-content;
  max-width: 100%;
  padding: 10px 16px;
  border-radius: 10px;
  box-shadow: 0 1px 3px 0 rgba(38, 55, 84, 0.08);
  background-color: #ffffff;
  margin-bottom: 3px;
  font-size: 12px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  text-align: left;
  color: #253655;
}

.message-own.you .message-sentence__own {
  background-color: #253655;
  color: #ffffff;
}

.message-sentence__time {
  font-size: 8px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  text-align: left;
  color: #a8aaac;
  padding: 0 8px;
}

.message-own.you .message-own__content {
  align-items: flex-end;
}

.message-own.you .message-sentence {
  align-items: flex-end;
}
