  padding: 3px 16px;
  border-radius: 5px;
  background-color: #e0f6ff;
  border: none;
  text-decoration: none;
}
.notifications-content__more a:hover {
  text-decoration: none;
}

.page {
  height: 100%;
}
.page-body {
  width: 100%;
  max-width: 100%;
  padding-left: 16px;
  padding-right: 16px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: column;
}

.page-sticky {
  position: -webkit-sticky;
  position: sticky;
  top: 64px;
  background-color: #ffffff;
  z-index: 100;
  padding-top: 20px;
}

.page-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 25px;
  /* border-bottom: 1px solid #eeeeee; */
}

.page-content__aside {
  display: flex;
  align-items: center;
}

.page-content__title {
  color: #000000;
  font-size: 30px;
  font-weight: 700;
}

.page-content__search {
  border-radius: 8px;
  border: 1px solid #eeeeee;
  display: flex;
  align-items: center;
  padding: 10px 20px;
  width: 225px;
  padding: 8px 16px;
  border-radius: 8px;
  border: solid 1px #f2f5f7;
  background-color: #ffffff;
  padding-right: 4px;
}

.page-content__search input {
  border: none;
  background-color: transparent;
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 0.24px;
  flex-grow: 1;
  font-size: 12px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  text-align: left;
  color: #a8aaac;
}
.page-content__search input::-webkit-input-placeholder {
  color: #bfbfbf;
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 0.24px;
}
.page-content__search input:-ms-input-placeholder {
  color: #bfbfbf;
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 0.24px;
}
.page-content__search input::-ms-input-placeholder {
  color: #bfbfbf;
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 0.24px;
}
.page-content__search input::placeholder {
  color: #bfbfbf;
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 0.24px;
}
.page-content__search button {
  background-color: transparent;
  border: none;
}

.page-content__search button svg {
  fill: #bfbfbf;
  width: 12px;
  height: 12px;
}

.page-tools {
  width: 100%;
  display: flex;
  align-items: center;
  margin-top: 25px;
  margin-bottom: 40px;
}

.page-tools__left {
  width: -webkit-min-content;
  width: -moz-min-content;
  width: min-content;
  flex-grow: 1;
}

.page-choose {
  border-radius: 5px;
  background-color: #ffffff;
  border: solid 1px #f2f5f7;
}

.page-choose__list {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
}

.page-choose__item {
  padding: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  border-radius: 5px;
}

.page-choose__item svg {
  fill: #a8aaac;
}

.page-choose__item.active {
  background-color: #f8f9fd;
}

.page-choose__item.active svg {
  fill: #00b9ff;
}

.page-filter__body {
  display: flex;
  align-items: center;
}

.page-filter__select {
  width: 150px;
  margin-left: 16px;
}

.page-filter__invite {
  margin-left: 16px;
}

.page-filter__select:first-child {
  margin-left: 0;
}

.page-filter__select .dropdown {
  width: 100%;
}

.page-filter__select .dropdown-toggle {
  background-color: #f8f9fd;
  border-radius: 5px;
  padding-top: 11px;
  padding-bottom: 11px;
  width: 100%;
}

.page-filter__select .dropdown-select {
  top: 44px;
}
.page-filter__create a,
.page-filter__create button {
  width: 100%;
  border: solid 1px #cbf0ff;
  background-color: #00b9ff;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  padding: 11px;
  color: #ffffff;
  font-size: 13px;
  font-weight: 500;
  text-decoration: none;
  border-radius: 4px;
  padding: 9px 31px 9.9px;
  border-radius: 8px;
  white-space: nowrap;
}

.page-filter__create a span,
.page-filter__create button span {
  margin-left: 10px;
}

.page-filter__create a svg,
.page-filter__create button svg {
  width: 15px;
  height: 15px;
  fill: #ffffff;
  flex-shrink: 0;
  /* margin-left: 20px; */
}

.page-filter__create button:focus {
  outline: none;
}

.page-teammates {
  width: 100%;
  height: 100%;
}
.page-teammates__body {
  /* display: flex;
  flex-wrap: wrap;
  margin-left: -25px;
  margin-right: -25px; */
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-row-gap: 60px;
  row-gap: 60px;
  grid-column-gap: 25px;
  -webkit-column-gap: 25px;
          column-gap: 25px;
  padding-bottom: 150px;
  padding-top: 50px;
}

.page-teammates__body.list {
  grid-template-columns: 1fr;
  grid-row-gap: 15px;
  row-gap: 15px;
  grid-column-gap: 0;
  -webkit-column-gap: 0;
          column-gap: 0;
}

.page-tools__delete {
  border: solid 1px #cbf0ff;
  background-color: #ffd8d8;
  font-size: 10px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.2;
  letter-spacing: normal;
  text-align: center;
  color: #f08686;
  padding: 11px;
  border: none;
}

.page-inner {
  width: 100%;
  height: -webkit-min-content;
  height: -moz-min-content;
  height: min-content;
  flex-grow: 1;
}

