.feedback {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1500;
  display: grid;
  height: 100%;
  place-items: center;
  text-align: center;
  background: #000;
}

.feedback-body {
  position: relative;
  width: 400px;
  background: #111;
  padding: 20px 30px;
  border: 1px solid #444;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.feedback .post {
  display: none;
}
.feedback .text {
  font-size: 25px;
  color: #666;
  font-weight: 500;
}
.feedback .edit {
  position: absolute;
  right: 10px;
  top: 5px;
  font-size: 16px;
  color: #666;
  font-weight: 500;
  cursor: pointer;
}
.feedback .edit:hover {
  text-decoration: underline;
}
.feedback .star-widget input {
  display: none;
}
.feedback .star-widget label {
  font-size: 40px;
  color: #444;
  padding: 10px;
  float: right;
  transition: all 0.2s ease;
}
.feedback input:not(:checked) ~ label:hover,
.feedback input:not(:checked) ~ label:hover ~ label {
  color: #fd4;
}
.feedback input:checked ~ label {
  color: #fd4;
}
.feedback input#rate-5:checked ~ label {
  color: #fe7;
  text-shadow: 0 0 20px #952;
}
#rate-1:checked ~ form header:before {
  content: "I just hate it ";
}
#rate-2:checked ~ form header:before {
  content: "I don't like it ";
}
#rate-3:checked ~ form header:before {
  content: "It is awesome ";
}
#rate-4:checked ~ form header:before {
  content: "I just like it ";
}
#rate-5:checked ~ form header:before {
  content: "I just love it ";
}
.feedback form {
  display: none;
}
.feedback input:checked ~ form {
  display: block;
}
.feedback form header {
  width: 100%;
  font-size: 25px;
  color: #fe7;
  font-weight: 500;
  margin: 5px 0 20px 0;
  text-align: center;
  transition: all 0.2s ease;
}
.feedback form .textarea {
  height: 100px;
  width: 100%;
  overflow: hidden;
}
.feedback form .textarea textarea {
  height: 100%;
  width: 100%;
  outline: none;
  color: #eee;
  border: 1px solid #333;
  background: #222;
  padding: 10px;
  font-size: 17px;
  resize: none;
}
.feedback .textarea textarea:focus {
  border-color: #444;
}
.feedback form .btn {
  height: 45px;
  width: 100%;
  margin: 15px 0;
}
.feedback form .btn button {
  height: 100%;
  width: 100%;
  border: 1px solid #444;
  outline: none;
  background: #222;
  color: #999;
  font-size: 17px;
  font-weight: 500;
  text-transform: uppercase;
  cursor: pointer;
  transition: all 0.3s ease;
}
.feedback form .btn button:hover {
  background: #1b1b1b;
}

