.tour-container-fluid{
    width: 960px;
    height: 652;
    margin-left: auto;
    margin-right: auto;
    text-align: center;

}



.tour-container-fluid h1{
    font-size: 35px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: bold;
    line-height: 50px;
    text-align: center;
}

.tour-container-fluid p{
    font-size: 20px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: normal;
    color: #707070;
    line-height: 24px;
    text-align: center;

}

.tour-container-fluid button{
    width: 600px;
    height: 70px;
    background-color: #0065FF;
    border: none;
    border-radius: 8px;
    color: red;
    font-size: 22px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: normal;
    color: white;
    line-height: 50px;
    margin-top: 40px;
    margin-bottom: 20px;
}


.tour-container-fluid .container-info{
    background-color: #F8F8FA;
    width: 876px;
    height: 350px;
    padding: 20px;
    border-radius: 12px;
    align-items: center;
    text-align: center;

}


.tour-container-fluid .text p {
    background-color: white;
    padding: 10px;
    color: #253655;
    font-size: 12px;
    border-radius: 12px;
    box-shadow: 10px 10px 5px #aaaaaa;
    display: block;
    text-align: justify;
}
.notification {
  width: 100%;
}
.notification-body {
  width: 100%;
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.04);
  background-color: #ffffff;
  border-radius: 15px;
  padding: 40px 0;
}

.notification-item {
  width: 100%;
  padding: 0 35px;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  cursor: pointer;
  display: grid;
  grid-template-columns: 60px 0.8fr 1fr 1.2fr 1fr 25px;
  white-space: nowrap;
}

.notification-item__image {
  height: 60px;
  border: solid 1px #707070;
  border-radius: 50%;
}
.notification-item__image img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  object-fit: cover;
}

.notification-item__name {
  /* width: min-content;
  flex-grow: 1; */
  padding-left: 20px;
}

.notification-item__text {
  font-size: 21px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.19;
  letter-spacing: normal;
  text-align: center;
  color: #042141;
}

.notification-item__name {
  text-align: left;
}

.notification-item__delete button {
  width: 24px;
  height: 24px;
  cursor: pointer;
  padding: 0;
  background-color: transparent;
  border: none;
}

.notification-item__delete svg {
  fill: #f08686;
  width: 100%;
  height: 100%;
}

.notification-item__time {
  /* width: 130px; */
  text-align: center;
  border-left: 2px solid #bfbfbf;
}

.notification-item__time span {
  display: block;
}

.notification-item__type {
  /* width: 300px; */
  display: flex;
  align-items: center;
  justify-content: center;
  border-left: 2px solid #bfbfbf;
}

.notification-item__type svg {
  width: 22px;
  height: 22px;
  fill: #f08686;
}

.notification-item__type span {
  margin-left: 10px;
}

.notification-item__status {
  /* width: 200px; */
  display: flex;
  align-items: center;
  justify-content: center;
  border-left: 2px solid #bfbfbf;
}
.notification-item__status span {
  margin-left: 10px;
}

.notification-item__status svg {
  width: 12px;
  height: 12px;
  fill: #f08686;
}

.notification-item__status svg.online {
  fill: #6ac000;
}

.notification-item__status svg.meeting {
  fill: #0a8aff;
}

@media only screen and (max-width: 1200px) {
  .notification-item__text span {
    font-size: 16px;
  }
}

@media only screen and (max-width: 1024px) {
  .notification-item {
    grid-template-columns: 30px 0.8fr 1fr 1.2fr 1fr 25px;
  }
  /* .notification-item__type {
    width: 200px;
  }
  .notification-item__status {
    width: 160px;
  } */
  /* .notification-item__text span {
    font-size: 16px;
  } */
  .notification-item__image {
    /* width: 30px; */
    height: 30px;
  }
}

.auth {
  width: 100%;
  height: 100vh;
  background-color: #ffffff;
}

.auth-body {
  width: 100%;
  height: 100%;
  display: flex;
  padding: 30px;
}

.auth-content {
  width: 100%;
  max-width: 980px;
  margin: auto;
}

.auth-head {
  width: 100%;
}

.auth-right {
  max-width: 465px;
  max-height: 335px;
}

.auth-right img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.auth-left {
  width: 100%;
  max-width: 320px;
}

.auth-logo {
  width: 100%;
  max-width: 320px;
}
.auth-logo img {
  width: 100%;
}

.auth-center {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.auth-left__button {
  width: 100%;
  max-width: 240px;
  height: 100%;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.auth-left__button a {
  width: 100%;
  padding: 16px 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  margin-bottom: 45px;
  font-size: 21px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.19;
  letter-spacing: 0.63px;
  text-align: left;
  text-decoration: none;
}

.auth-left__button a.auth-create {
  background-color: #00b9ff;
  color: #ffffff;
}

.auth-left__button a.auth-join {
  border: solid 1px #00b9ff;
  background-color: #ffffff;
  color: #00b9ff;
}

.auth-inner {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.auth-title {
  width: 100%;
  max-width: 760px;
  text-align: center;
  margin-top: 60px;
  margin-bottom: 60px;
}

.auth-title h1 {
  font-size: 49px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.2;
  letter-spacing: normal;
  text-align: left;
  color: #000000;
  display: block;
  text-align: center;
}

.auth-title span {
  font-size: 12px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.17;
  letter-spacing: normal;
  text-align: left;
  color: #000000;
}

.auth-title.invite {
  margin-bottom: 20px;
}

.auth-title.invite h1 {
  font-size: 34px;
}

.auth-form {
  width: 100%;
}

.auth-form__content {
  width: 100%;
  max-width: 285px;
  margin-left: auto;
  margin-right: auto;
}

.auth-form__group {
  width: 100%;
  margin-bottom: 24px;
}
.auth-form__group button {
  width: 100%;
}
.auth-form__group button.social {
  border-radius: 10px;
  border: solid 1px #000000;
  padding: 14px;
  background-color: transparent;
  font-size: 18px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.17;
  letter-spacing: normal;
  color: #000000;
  margin-bottom: 55px;
}

.auth-form__group__error > input{
  border: 1px solid #e91e63!important;
}

.auth-or {
  width: 100%;
  height: 2px;
  background-color: #bfbfbf;
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}
.auth-or__content {
  width: -webkit-min-content;
  width: -moz-min-content;
  width: min-content;
  padding: 10px 20px;
  background-color: #ffffff;
  font-size: 11px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.18;
