.invitepeople {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 140;
  background-color: rgba(37, 54, 85, 0.4);
}

.invitepeople-body {
  width: 100%;
  height: 100%;
  display: flex;
}

.invitepeople-modal {
  width: 100%;
  max-width: 360px;
  height: min-content;
  margin: auto;
  border-radius: 15px;
  box-shadow: 0 12px 24px 0 rgba(38, 55, 84, 0.1);
  background-color: #ffffff;
  padding: 24px;
  position: relative;
}

.invitepeople-modal__close {
  width: 10px;
  height: 10px;
  position: absolute;
  top: 16px;
  right: 16px;
  cursor: pointer;
}

.invitepeople-modal__close svg {
  width: 100%;
  height: 100%;
  fill: #a8aaac;
}

.invitepeople-header {
  width: 100%;
  display: flex;
  align-items: center;
}

.invitepeople-header__emoji {
  width: 43px;
  height: 43px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.invitepeople-header__emoji span {
  font-size: 20px;
}

.invitepeople-header__about {
  width: 200px;
  padding-left: 10px;
}

.invitepeople-header__title {
  width: 100%;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  font-size: 14px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  text-align: left;
  color: #253655;
}

.invitepeople-header__count {
  opacity: 0.7;
  font-size: 10px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.6;
  letter-spacing: normal;
  text-align: left;
  color: #253655;
}

.invitepeople-add {
  width: 100%;
  margin: 18px 0;
}

.invitepeople-add button {
  background-color: transparent;
  padding: 0;
  border: none;
  font-size: 12px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  text-align: left;
  color: #00b9ff;
}

.invitepeople-search {
  width: 100%;
  position: relative;
  margin-bottom: 24px;
}

.invitepeople-search input {
  width: 100%;
  border-radius: 10px;
  border: solid 1px #f2f5f7;
  padding: 12px 16px;
  padding-right: 32px;
  font-size: 12px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  text-align: left;
  color: #253655;
}

.invitepeople-search input::placeholder {
  opacity: 0.32;
}

.invitepeople-search svg {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  fill: #253655;
  opacity: 0.32;
  cursor: pointer;
}

.invitepeople-men {
  width: 100%;
  max-height: 300px;
  overflow: scroll;
  overflow-x: hidden;
}

.invitepeople-men::-webkit-scrollbar {
  width: 7px;
}

/* Track */
.invitepeople-men::-webkit-scrollbar-track {
  /* box-shadow: inset 0 0 5px grey; */
  border-radius: 10px;
  background-color: #cccccc;
}

/* Handle */
.invitepeople-men::-webkit-scrollbar-thumb {
  background: #a8aaac;
  border-radius: 10px;
}

/* Handle on hover */
/* .invitepeople-men::-webkit-scrollbar-thumb:hover {
  background: #b30000;
} */

.invitepeople-men__list {
  width: 100%;
  list-style: none;
  padding: 0;
}
.invitepeople-men__item {
  width: 100%;
  display: flex;
  align-items: center;
  padding: 8px;
}

.invitepeople-men__item--name {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  border: solid 2px #eeeeee;
  text-transform: uppercase;
}

.invitepeople-men__item img {
  width: 32px;
  height: 32px;
  object-fit: cover;
  border-radius: 50%;
}
.invitepeople-men__item button {
  background-color: transparent;
  border: none;
  padding: 0;
  font-size: 12px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  text-align: right;
  color: #409a5d;
}

.invitepeople-men__item button.added {
  color: #00b9ff;
  cursor: pointer;
}

.invitepeople-men__item span {
  width: min-content;
  flex-grow: 1;
  padding: 0 10px;
  font-size: 13px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.54;
  letter-spacing: normal;
  text-align: left;
  color: #253655;
}
