.tour-container-fluid{
    width: 960px;
    height: 652;
    margin-left: auto;
    margin-right: auto;
    text-align: center;

}



.tour-container-fluid h1{
    font-size: 35px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: bold;
    line-height: 50px;
    text-align: center;
}

.tour-container-fluid p{
    font-size: 20px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: normal;
    color: #707070;
    line-height: 24px;
    text-align: center;

}

.tour-container-fluid button{
    width: 600px;
    height: 70px;
    background-color: #0065FF;
    border: none;
    border-radius: 8px;
    color: red;
    font-size: 22px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: normal;
    color: white;
    line-height: 50px;
    margin-top: 40px;
    margin-bottom: 20px;
}


.tour-container-fluid .container-info{
    background-color: #F8F8FA;
    width: 876px;
    height: 350px;
    padding: 20px;
    border-radius: 12px;
    align-items: center;
    text-align: center;

}


.tour-container-fluid .text p {
    background-color: white;
    padding: 10px;
    color: #253655;
    font-size: 12px;
    border-radius: 12px;
    box-shadow: 10px 10px 5px #aaaaaa;
    display: block;
    text-align: justify;
}